.App {
  text-align: center;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-y: auto;
}

.App .Background {
  background: radial-gradient( at 50% 70%, #200f20 40%, #090b1f 80%, #050523 100% );
  /* z-index: -2; */
  position: fixed;
  top: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App > .ui {
  display: flex;
  flex-direction: column;
}

.parallax-item > h1 {
  color: #ffffffba;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  line-height: 110%;
}

.Screen-size {
  height: 100vh;
  width: 100vw;
}

.parallax-container {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  position: relative;
  -webkit-perspective: 1px;
  perspective: 1px;
}

.parallax-container-new {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  position: absolute;
  -webkit-perspective: 1px;
  perspective: 1px;
}


.parallax-group {
  transform-style: preserve-3d;
}

.parallax-item {
  padding: 100vh 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.item--1 {
  padding: 0!important;
}

.item--2 {
  padding: 0!important;
}

.item--1 > * {
  top: 66%!important;
}

.item--2 > * {
  color: #ffffff63;
  scale: 1 -1;
  top: 65%!important;
}

.Three-canvas-container {
  position: relative;
  top: 0;
  height: 100%;
  /* z-index: -1; */
}

.planet-ini {
  -webkit-transform: translateZ(-1px) scale(2);
  transform: translateZ(-1px) scale(3);
}

.bang-messi {
  -webkit-transform: translateZ(-1px) scale(2);
  transform: translateZ(-2px) scale(4);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.fixed {
  position: fixed;
}

a.fixed {
  font-weight: 400;
  font-size: 18px;
  color: inherit;
  display: inline;
  text-decoration: none;
  z-index: 1;
  color: white;
}

a.top-left {
  top: 60px;
  left: 60px;
}

a.top-right {
  top: 60px;
  right: 60px;
}

.content {
  height: 100%;
  position: relative;
}